	module.exports = {"pathContext":{"docHtml":"<h1>ignore-by-default</h1>\n<p>This is a package aimed at Node.js development tools. It provides a list of\ndirectories that should probably be ignored by such tools, e.g. when watching\nfor file changes.</p>\n<p>It's used by <a href=\"https://www.npmjs.com/package/ava\">AVA</a> and\n<a href=\"https://www.npmjs.com/package/nodemon\">nodemon</a>.</p>\n<p><a href=\"./CONTRIBUTING.html\">Please contribute!</a></p>\n<h2>Installation</h2>\n<pre><code>npm install --save ignore-by-default\n</code></pre>\n<h2>Usage</h2>\n<p>The <code>ignore-by-default</code> module exports a <code>directories()</code> function, which will\nreturn an array of directory names. These are the ones you should ignore.</p>\n<pre><code class=\"language-js\">// ['.git', '.sass_cache', …]\nvar ignoredDirectories = require('ignore-by-default').directories()\n</code></pre>","docPath":"argo-ci/node_modules/ignore-by-default/readme","proj":"argo-ci"}}

